/* Fancy Button */
.fancy-button-container {
  margin-top: 20px;
  /* height: 150px; */
}
.fancy-button-container a {
  position: relative;
  display: inline-block;
  padding: 15px 20px;
  color: #fc6d1b;
  font-size: 16px;
  text-decoration: none;
  text-transform: uppercase;
  overflow: hidden;
  transition: 0.5s;
  /* margin-top: 20px; */
  letter-spacing: 2px;
  height: fit-content;
}

.fancy-button-container a:hover {
  background: #fc6d1b;
  color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px #fc6d1b, 0 0 25px #fc6d1b, 0 0 50px #fc6d1b,
    0 0 100px #fc6d1b;
}

.fancy-button-container a span {
  position: absolute;
  display: block;
}

.fancy-button-container a span:nth-child(1) {
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #fc6d1b);
  animation: btn-anim1 1s linear infinite;
}

@keyframes btn-anim1 {
  0% {
    left: -100%;
  }
  50%,
  100% {
    left: 100%;
  }
}

.fancy-button-container a span:nth-child(2) {
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #fc6d1b);
  animation: btn-anim2 1s linear infinite;
  animation-delay: 0.25s;
}

@keyframes btn-anim2 {
  0% {
    top: -100%;
  }
  50%,
  100% {
    top: 100%;
  }
}

.fancy-button-container a span:nth-child(3) {
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, transparent, #fc6d1b);
  animation: btn-anim3 1s linear infinite;
  animation-delay: 0.5s;
}

@keyframes btn-anim3 {
  0% {
    right: -100%;
  }
  50%,
  100% {
    right: 100%;
  }
}

.fancy-button-container a span:nth-child(4) {
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg, transparent, #fc6d1b);
  animation: btn-anim4 1s linear infinite;
  animation-delay: 0.75s;
}

@keyframes btn-anim4 {
  0% {
    bottom: -100%;
  }
  50%,
  100% {
    bottom: 100%;
  }
}

