    margin-left: 6.697vw;

    justify-content: center;
    max-width: 300px;
  }
}

/* Order Confirmation */
#order-confirmation-parent {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  /* transform: translateY(70%); */
  /* height: 100%; */

  /* visibility: hidden; */

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: hsl(var(--clr-black), 0.56);

  backdrop-filter: blur(5px);
  z-index: 100000000;
}

#order-confirmation {
  max-width: 660px;
  width: 37.5vw;
  padding: 3rem;
  border-radius: 25px;

  display: flex;
  flex-direction: column;
  gap: 2rem;
  text-align: start;
  justify-content: center;
  align-items: flex-start;

  background-color: white;
  opacity: 1;
  margin-top: 50px;

  animation: scaling 0.1s ease 1;
}

@keyframes scaling {
  0% {
    scale: 0;
  }

  100% {
    scale: 1;
  }
}

#order-confirmation > *:last-child {
  margin-top: 1rem;
  width: 100%;
}

.order-preview-container {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.order-preview {
  display: flex;
  flex-direction: column;
  background-color: #f1f1f1;
  padding: 1.5rem;
  flex: 1;
  gap: 12px;

  border-radius: 8px 0px 0px 8px;
}

.order-preview-line {
  height: 1px;
  border: 1px solid hsl(var(--clr-black), 0.086);
}

.order-preview-items {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.order-preview-items div {
  min-width: 99px;
}
.order-preview-items h1 {
  line-height: 25px;
  font-weight: 700;
}

.order-preview-items h2 {
  line-height: 25px;
  color: black;
  opacity: 0.5;
  letter-spacing: normal;
}

.order-preview-items h3 {
  color: #000;
  text-align: right;
  font-size: 15px;
  font-style: normal;
  font-weight: 700;
  line-height: 25px;
  opacity: 0.5;
  margin-left: 2rem;
}

.order-preview-items img {
  max-width: 3.47222vw;
  aspect-ratio: 1;
}

.order-preview-total {
  background-color: #000;
  flex: 0.8181;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 0.5rem;
  padding-inline: 32px 30px;
  padding-block: 41px;

  border-radius: 0px 8px 8px 0px;
}

.order-preview-total h1 {
  letter-spacing: normal;
  opacity: 0.5;
}

.other-items {
  display: flex;
  width: 100%;
  justify-content: center;
}

.other-items span {
  font-size: 12px;
  font-weight: 700;
  line-height: normal;
  letter-spacing: -0.214px;
  opacity: 0.5;
}

.processing {
  color: hsl(var(--clr--lightblack));
}

@media only screen and (max-width: 64em) {
  #order-confirmation {
    max-width: 560px;
    width: 70.31vw;
    padding: 3rem;

    margin-top: 0px;
  }

  .order-preview-items img {
    max-width: 3.9222vw;
    aspect-ratio: 0.95;
  }

  .order-preview-items h3 {
    margin-left: 0.2rem;
  }
}

@media only screen and (max-width: 37em) {
  .order-preview-container {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  #order-confirmation {
    max-width: 400px;
    width: 87.31vw;
    padding: 2rem;
  }

  .order-preview-items {
    justify-content: space-evenly;
  }

  .order-preview-items img {
    min-width: 28px;
    height: 32px;
  }

  .order-preview {
    border-radius: 8px 8px 0px 0px;
  }

  .order-preview-total {
    background-color: #000;
    flex: 0.661;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 0.5rem;
    padding-inline: 1.6rem;
    padding-block: 1rem;

    border-radius: 0px 0px 8px 8px;
  }
}

.spinner-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
}
.chakra-spinner {
  width: 50px;
  aspect-ratio: 1;
  margin-right: 10px;
}
